import { PhotoGuide } from "./index";
import PhotoGuideComponent from "./PhotoGuide";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../../../redux/store";
import {
  useCreateHorseMutation,
  useGetHorseQuery,
  useUpdateHorseMutation,
  useUploadImageMutation,
} from "../../../redux/api/horseApi";
import React, { useEffect, useState } from "react";
import { LoadingSpinner } from "../../LoadingSpinner";
import { patchSelectedHorse } from "../../../redux/appState/appStateSlice";
import { isError } from "lodash";
import { Alert } from "@mui/material";
import { parseTargetNew } from "./parse";
import { navigate } from "gatsby";
import routes from "../../../constants/routes";
import { Horse } from "../../../redux/types";

type PhotoGuideDataProps = {
  cms: any;
  horseId: string;
  pos: string | null;
  target: string | null;
};

export const PhotoGuideData = ({ cms, horseId }: PhotoGuideDataProps) => {
  const user = useSelector((state: RootState) => state.auth.user);
  const appState = useSelector((state: RootState) => state.appState);

  const dispatch = useDispatch();

  const { pos, target } = appState.selectedHorse;

  if (typeof pos === undefined || !target) {
    // @ts-ignore
    navigate(routes.detailView(horseId));
  }

  const {
    data: horseData,
    isLoading,
    isError,
  } = useGetHorseQuery(
    { userId: user?.id as string, horseId: horseId },
    {
      refetchOnMountOrArgChange: true,
      skip: !horseId || !user?.id,
    }
  );

  const image =
    target && typeof pos !== undefined && target === "torso"
      ? horseData?.images?.torso
      : horseData?.images?.[target as string]?.[pos as number];

  const [updateHorse, updateHorseResult] = useUpdateHorseMutation();
  const [uploadImage, uploadImageResult] = useUploadImageMutation();

  const [completed, setCompleted] = useState(false);

  const saveImage = (index: number) => {
    const image = appState.selectedHorse.temporaryImages[index];

    uploadImage({
      userId: user?.id,
      horseId,
      data: {
        image,
      },
    });
  };

  useEffect(() => {
    console.log(
      "checking, ",
      uploadImageResult.isSuccess && typeof pos !== undefined && target
    );
    if (uploadImageResult.isSuccess && typeof pos !== undefined && target) {
      const url = uploadImageResult?.data.url;
      console.log("1");
      setCompleted(true);
      console.log(
        "2",
        target === "torso"
          ? ({
              images: {
                torso: url,
              },
            } as Partial<Horse>)
          : ({
              images: {
                [target]: {
                  [pos as number]: url,
                },
              },
            } as Partial<Horse>)
      );

      updateHorse({
        userId: user?.id as string,
        horseId,
        data:
          target === "torso"
            ? ({
                images: {
                  torso: url,
                },
              } as Partial<Horse>)
            : ({
                images: {
                  [target]: {
                    [pos as number]: url,
                  },
                },
              } as Partial<Horse>),
      });
      console.log("3");

      dispatch(patchSelectedHorse({ selectedHorse: { temporaryImages: [] } }));
    }
  }, [uploadImageResult]);

  useEffect(() => {
    if (updateHorseResult.isSuccess) {
      dispatch(
        patchSelectedHorse({
          selectedHorse: {
            target: undefined,
            pos: undefined,
          },
        })
      );
    }
  }, [updateHorseResult]);

  if (isLoading || uploadImageResult.isLoading || updateHorseResult.isLoading) {
    return <LoadingSpinner />;
  }

  if (isError) {
    return (
      <Alert severity={"error"}>Daten konnten nicht geladen werden.</Alert>
    );
  }

  if (updateHorseResult.isError) {
    return (
      <Alert severity={"error"}>Das Bild konnte nicht zugeordnet werden.</Alert>
    );
  }

  if (uploadImageResult.isError) {
    return (
      <Alert severity={"error"}>
        Das Bild konnte nicht gespeichert werden.
      </Alert>
    );
  }

  return (
    <PhotoGuideComponent
      pos={pos}
      target={target}
      images={image ? [image] : appState.selectedHorse.temporaryImages}
      cms={cms}
      completed={completed}
      reset={undefined}
      patchSelectedHorse={(target: string, pos: number) =>
        dispatch(patchSelectedHorse({ selectedHorse: { target, pos } }))
      }
      horse={horseData}
      saveImage={saveImage}
    />
  );
};
