export function parseTargetNew(target) {
  switch (target) {
    case "leg0":
      return "Bein_VR";
    case "leg1":
      return "Bein_VL";
    case "leg2":
      return "Bein_HR";
    case "leg3":
      return "Bein_HL";
    case "torso":
      return "Koerper";
  }
}

