export function parsePosNew(pos) {
  switch (pos) {
    case "0":
    case 0:
      return "Koerper";
    case "1":
    case 1:
      return "Fessel";
    case "2":
    case 2:
      return "Hufbreite";
    case "3":
    case 3:
      return "Huflaenge";
  }
}

